<script>
import profile from './UserProfile'
export default {
  name: 'Demo',
  data () {
    return {
      alertType: 'info',
      alertIcon: true,
      position: 'bottomRight',
      toastType: 'info',
      toastIcon: true
    }
  },
  methods: {
    callModal () {
      this.$dlg.modal(profile, {
        width: 350,
        height: 450,
        title: 'User profile',
        callback: data => {
          this.$dlg.alert(`The user company is: <b>${data.companyName ? data.companyName : 'Not Selected'}</b>`)
        },
        params: {
          name: 'Terry Zeng'
        }
      })
    },
    callAlert () {
      const option = {
        messageType: this.alertType,
        icon: this.alertIcon
      }
      if (this.alertType === 'confirm') {
        option.cancelCallback = () => {
          this.$dlg.alert('You canceled confirm dialog!')
        }
      }
      this.$dlg.alert(`This is a Alert dialog, messageType is ${this.alertType}`, option)
    },
    callMask () {
      this.$dlg.mask(null, {
        closeTime: 3
      })
    },
    callToast () {
      this.$dlg.toast(`This is a Toast dialog, messageType is ${this.toastType}`, {
        messageType: this.toastType,
        closeTime: 3,
        position: this.position,
        icon: this.toastIcon
      })
    }
  }
}
</script>
